import { Component, OnInit, ContentChild, AfterContentInit, Input, ViewChild, ElementRef } from '@angular/core'
import * as MindFusion from 'diagram-library';
import { DiagramView } from './diagram-library-angular.component';

@Component({
    selector: 'ruler',
    template: `<div #id>
                   <ng-content></ng-content>
               </div>`
})
export class Ruler implements AfterContentInit {

    @ContentChild(DiagramView) input: DiagramView;
    @ViewChild('id', { static: true }) div_id: ElementRef<HTMLDivElement>;
    @Input('unit') gr_unit: MindFusion.Drawing.GraphicsUnit = MindFusion.Drawing.GraphicsUnit.Millimeter;

    @Input('enableGuides') en_guides: boolean = true;
    @Input('projectRotatedBounds') pr_bounds: boolean;
    @Input('negatedY') neg_y: boolean;
    @Input('negatedX') neg_x: boolean;
    @Input('verticalScaleVisible') vert_visible: boolean = true;
    @Input('horizontalScaleVisible') hor_visible: boolean = true;

    @Input('id') ruler_id: string;
    @Input('pointerColor') pointer_color: string = "red";
    @Input('textColor') text_color: string = "#000000";
    @Input('guideColor') guide_color: string = "#000000";
    @Input('projectionColor') projection_color: string = "lightSteelBlue";
    @Input('backColor') back_color: string = "#dddddd";
    @Input('foreColor') fore_Color: string = "#000000";


    ngAfterContentInit(): void {


        if (this.ruler_id === undefined)
            this.ruler_id = "ruler";
        this.div_id.nativeElement.id = this.ruler_id;
        var ruler = new MindFusion.Diagramming.Ruler(document.getElementById(this.ruler_id));
        if (this.input && (this.input instanceof DiagramView)) {
            ruler.setDiagram(this.input.diagram);
        } else {
            console.error('ruler requirs diagram-view');
        }

        ruler.setUnit(this.gr_unit);
        ruler.setEnableGuides(this.en_guides);
        ruler.setProjectRotatedBounds(this.pr_bounds);
        ruler.setNegatedX(this.neg_x);
        ruler.setNegatedY(this.neg_y);
        ruler.setVerticalScaleVisible(this.vert_visible);
        ruler.setHorizontalScaleVisible(this.hor_visible);
        ruler.setPointerColor(this.pointer_color);
        ruler.setTextColor(this.text_color);
        ruler.setGuideColor(this.guide_color);
        ruler.setProjectionColor(this.projection_color);
        ruler.setBackColor(this.back_color);
        ruler.setForeColor(this.fore_Color);


    }

}