import {Component, AfterViewInit, Input, ViewChild, ElementRef} from '@angular/core';
import * as MindFusion from 'diagram-library';
import NodeList = MindFusion.Diagramming.NodeListView;

@Component({
    selector: 'node-list-view',
    template: `<div style="overflow-y: auto; overflow-x: hidden; width: 230px; height: 200px;">
                 <canvas #nodeList width="230" height="200" ></canvas>
               </div>`
})
export class NodeListView implements AfterViewInit{

    @ViewChild('nodeList', { static: true }) canvas: ElementRef<HTMLCanvasElement>;
    @Input('id') node_id: string;
    @Input('enabled') enabled: boolean = true;
    @Input('iconSize') icon_size : MindFusion.Drawing.Size = new MindFusion.Drawing.Size(24,24);
    @Input('defaultNodeSize') node_size : MindFusion.Drawing.Size = new MindFusion.Drawing.Size(24,24);
    @Input('padding') padding: number = 5;
    @Input('orientation') orientation : MindFusion.Diagramming.Orientation = MindFusion.Diagramming.Orientation.Vertical;
    @Input('tooltipDelay') tooltipDelay : number = 1000;
    @Input('shapeLibraryLocation') shapeLibraryLocation : string ="";
    @Input('captions') caption : string[];
    @Input('nodes') nodes : MindFusion.Diagramming.DiagramItem[];

    nodeList : NodeList;

    ngAfterViewInit(): void{

        if(this.node_id === undefined)
            this.node_id = "nodeList";

        this.canvas.nativeElement.id = this.node_id;    
        this.nodeList = MindFusion.Diagramming.NodeListView.create(document.getElementById(this.node_id));
        this.nodeList.setEnabled(this.enabled);
        this.nodeList.setIconSize(this.icon_size);
        this.nodeList.setDefaultNodeSize(this.node_size);
        this.nodeList.setPadding(this.padding);
        this.nodeList.setOrientation(this.orientation);
        this.nodeList.setShapeLibraryLocation(this.shapeLibraryLocation);
        this.nodeList.setTooltipDelay(this.tooltipDelay)
        this.addNode(this.nodes, this.caption);

    }

    private addNode(nodes, caption) {

        for (var i = 0; i < nodes.length; ++i) {
            this.nodeList.addNode(nodes[i], caption[i]);
        }
    }
}