import { Component, AfterViewInit, Input, ViewChild, ElementRef } from '@angular/core';
import * as MindFusion from 'diagram-library';
import Diagram = MindFusion.Diagramming.Diagram;
@Component({
    selector: 'zoom-control',
    template: `<div style="width: 50px; height: 300px;">
                  <canvas #zoomer width="50px" height="300px"></canvas>
               </div>`
})
export class ZoomControl implements AfterViewInit{

    @ViewChild('zoomer', { static: true }) canvas: ElementRef<HTMLCanvasElement>;
    @Input('id') zoom_id : string;
    @Input('diagram') diagram : Diagram;
    @Input('enabled') enabled : boolean = true;
    @Input('cornerRadius') cornerRadius : number = 2;
    @Input('padding') padding : number = 2;
    @Input('snapToZoomStep') snapToZoomStep : boolean = true;
    @Input('tickPosition') tick_Position : MindFusion.Controls.TickPosition = MindFusion.Controls.TickPosition.Left;
    @Input('showLabel') show_label : boolean = true;
    @Input('textColor') txt_color : string = "rgba(77,83,94,1)";
    @Input('shadowColor') sh_color : string = "gray";
    @Input('innerColor') inn_color : string = "rgba(91,91,91,1)";
    @Input('borderColor') br_color :string = "rgba(77,83,94,1)";
    @Input('activeColor') act_Color : string = "skyBlue";
    @Input("fill") fl: string = "#FFFFFF";
    @Input('backColor') back_color: string = "#FFFFFF";
    @Input('scrollStep') scr_step : number = 10;
    @Input('zoomStep') z_step : number = 10;
    @Input('maxZoomFactor') mx_Z_fac : number = 200;
    @Input('minZoomFactor') mn_Z_fac : number = 0;
    @Input('zoomFactor') z_fac: number = 100;



    ngAfterViewInit(): void{

        if(this.zoom_id === undefined)
            this.zoom_id = "zoomer";
        
        this.canvas.nativeElement.id = this.zoom_id;    
        var zoomer = MindFusion.Controls.ZoomControl.create(document.getElementById(this.zoom_id));
        zoomer.setCornerRadius(this.cornerRadius);
        zoomer.setPadding(this.padding);
        zoomer.setSnapToZoomStep(this.snapToZoomStep);
        zoomer.setTickPosition(this.tick_Position);
        zoomer.setShowLabel(this.show_label);
        zoomer.setTextColor(this.txt_color);
        zoomer.setShadowColor(this.sh_color);
        zoomer.setInnerColor(this.inn_color);
        zoomer.setBorderColor(this.br_color);
        zoomer.setActiveColor(this.act_Color);
        zoomer.setFill(this.fl);
        zoomer.setBackColor(this.back_color);
        zoomer.setScrollStep(this.scr_step);
        zoomer.setZoomStep(this.z_step);
        zoomer.setMaxZoomFactor(this.mx_Z_fac);
        zoomer.setMinZoomFactor(this.mn_Z_fac);
        zoomer.setZoomFactor(this.z_fac);
        zoomer.setEnabled(true);
        zoomer.setTarget(this.diagram);

    }
}