import { Component, OnInit, OnDestroy, ViewChild, Input, Output, EventEmitter, ElementRef } from '@angular/core';
import * as Common from 'mindfusion-common';
import * as MindFusion from 'diagram-library';

import Diagram = MindFusion.Diagramming.Diagram;

@Component({
  selector: 'diagram-view',
  template: `
    <div style="position: absolute; overflow: auto; width: auto; height: auto; top: 0px; bottom: 0px; left: 0px; right: 0px; text-align: left;">
       <canvas #canvas></canvas>
    </div>`,
})
export class DiagramView implements OnInit, OnDestroy {

  
  @ViewChild('canvas', { static: true })
  canvas: ElementRef<HTMLCanvasElement>; 

  @Input('id') passedID: string;    
  @Input("diagram") diagram : Diagram
  @Input("allowInplaceEdit") inplaceEdit : boolean = false;
  @Input("enabled") enabled : boolean = true;
  @Input("roundedLinks") roundedLinks : boolean = false;
  @Input("routeLinks") routeLinks : boolean = false;
  @Input("showGrid") showGrid : boolean = false;

  @Input("autoResize") autoResize : MindFusion.Diagramming.AutoResize = MindFusion.Diagramming.AutoResize.None;
  @Input("behavior") behavior : MindFusion.Diagramming.Behavior = MindFusion.Diagramming.Behavior.LinkShapes;
  @Input("linkShape") linkShape : MindFusion.Diagramming.LinkShape = MindFusion.Diagramming.LinkShape.Polyline;
  @Input("showAnchors") showAnchors : MindFusion.Diagramming.ShowAnchors=MindFusion.Diagramming.ShowAnchors.Auto;

  @Input("backBrush") backBrush : string;
  @Input("defaulShape") defaultShape : string = "Rectangle";
  @Input("linkHeadShapeSize") linkHeadShapeSize : number = 5;

  @Output("onNodeCreated") nodeCreated: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onInitializeLink") initializeLink: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onInitializeNode") initializeNode: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onLinkClicked") linkClicked: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onLinkCreated") linkCreated: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onLinkDeleted") linkDeleted: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onLinkDoubleClicked") linkDoubleClicked: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onLinkModified") linkModified: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onLinkPointed") linkPointed: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onLinkTextEdited") linkTextEdited: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onNodeClicked") nodeClicked: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onNodeDeleted") nodeDeleted: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onNodeDoubleClicked") nodeDoubleClicked: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onNodeModified") nodeModified: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onNodePointed") nodePointed: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onNodeTextEdited") nodeTextEdited: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onLinkCreating") linkCreating: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onLinkDeleting") linkDeleting: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onLinkModifying") linkModifying: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onNodeCreating") nodeCreating: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onNodeDeleting") nodeDeleting: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onNodeModifying") nodeModifying: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onContainerChildAdded") containerChildAdded: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onContainerChildAdding") containerChildAdding: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onContainerChildRemoving") containerChildRemoving: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onContainerChildRemoved") containerChildRemoved: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onContainerFolded") containerFolded: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onContainerUnfolded") containerUnfolded: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onTreeExpanded") treeExpanded: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onTreeCollapsed") treeCollapsed: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onSelectionModifying") selectionModifying: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onRepaint") repaint: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onSizeChanged") sizeChanged: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onNodeSelected") nodeSelected: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onNodeDeselected") nodeDeselected: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onLinkSelected") linkSelected: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onLinkDeselected") linkDeselected: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onControlLoaded") controlLoaded: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onHitTestAdjustmentHandles") hitTestAdjustmentHandles: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onDrawAdjustmentHandles") drawAdjustmentHandles: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onClicked") clicked: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onEnterInplaceEditMode") enterInplaceEditMode: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onLeaveInplaceEditMode") leaveInplaceEditMode: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onCreateEditControl") createEditControl: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onCellTextEdited") cellTextEdited: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onLaneGridCellTextEdited") laneGridCellTextEdited: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onSerializeTag") serializeTag: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onDeserializeTag") deserializeTag: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onTableColumnResizing") tableColumnResizing: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onTableColumnResized") tableColumnResized: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onTableRowResizing") tableRowResizing: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onTableRowResized") tableRowResized: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onNodePasted") nodePasted: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onLinkPasted") linkPasted: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onHeaderStartResizing") headerStartResizing: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onHeaderResized") headerResized: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onItemAdded") itemAdded: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onItemRemoved") itemRemoved: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onAnimatedLayoutCompleted") animatedLayoutCompleted: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onHyperlinkClicked") hyperlinkClicked: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();
  @Output("onExpandButtonClicked") expandButtonClicked: EventEmitter<{sender : DiagramView, args : any}> = new EventEmitter<{sender : DiagramView, args : any}>();



  constructor() { }

  ngOnInit(): void {

    // bind properties
    if(this.passedID === undefined)
        this.passedID = "diagram";
    this.canvas.nativeElement.id = this.passedID;
    MindFusion.AbstractionLayer.attachControl(this.diagram, document.getElementById(this.passedID));
    this.diagram.setAllowInplaceEdit(this.inplaceEdit);
    this.diagram.setAutoResize(this.autoResize);
    this.diagram.setBackBrush(this.backBrush);
    this.diagram.setBehavior(this.behavior);
    this.diagram.setDefaultShape(MindFusion.Diagramming.Shape.fromId(this.defaultShape))
    this.diagram.setEnabled(this.enabled)
    this.diagram.setLinkHeadShapeSize(this.linkHeadShapeSize);
    this.diagram.setLinkShape(this.linkShape);
    this.diagram.setRoundedLinks(this.roundedLinks)
    this.diagram.setRouteLinks(this.routeLinks)
    this.diagram.setShowAnchors(this.showAnchors);
    this.diagram.setShowGrid(this.showGrid);
    // bind events
    var events = MindFusion.Diagramming.Events;
    for(var v in events){
      function addAndDelegate(event, prop){
        prop.diagram.addEventListener(event, (sender, args) => {
          let emitter = Reflect.get(prop, event);
          emitter.emit({sender, args});
        });
      }
      addAndDelegate(v, this);
    }
  }

  ngOnDestroy(): void {
    if(this.diagram){
      this.diagram.dispose();
    }
  }

}
